.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes toast-fade {
  from {
    opacity: 1;
  }
  to {
    opacity:0.15;
  }
}

@keyframes body-fade {
  from {
    opacity: 1;
  }
  to {
    opacity:0;
    display: none;
  }
}
